
import Board from '@/components/Board.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { mapState, mapMutations, mapActions, mapGetters } from 'vuex'

export default {
name: 'ValGuesser',
components: {
Board,
XButton,
Spinner
},
data() {
return {
gameState: 'guessing', // guessing, calculating, result, finished
currentProblemIndex: 1,
totalScore: 0,
lastRoundScore: 0,

guessInput: '', // String for input handling
guessScore: 0,  // Parsed number
actualScore: 0,
currentDepth: 0,
engineReady: false
}
},
computed: {
...mapState('position', ['position', 'size', 'board']),
...mapState('ai', ['outputs', 'ready']),
...mapGetters('position', ['get']),
},
watch: {
outputs: {
handler(val) {
console.log(JSON.stringify(val))
if (this.gameState === 'calculating' && val.pv && val.pv.length > 0) {
const info = val.pv[0]
this.currentDepth = info.depth

const evalStr = info.eval

const isMate = evalStr && (evalStr.startsWith('M') || evalStr.startsWith('+M') || evalStr.startsWith('-M'))

if ((info.nodes >= 2500000 || isMate) && evalStr && evalStr !== '-') {
this.stop()


let evalScore = 0
if (isMate) {
evalScore = evalStr.startsWith('-') ? -10000 : 10000
} else {
evalScore = parseInt(evalStr)

if (isNaN(evalScore)) evalScore = 0
}

this.actualScore = evalScore
this.calculateScore()

if (this.currentProblemIndex >= 10) {
this.gameState = 'finished'
} else {
this.gameState = 'result'
}
}
}
},
deep: true
},
ready(val) {
if (val && !this.engineReady) {
this.engineReady = true
}
}
},
mounted() {
this.restartGame()
},
methods: {
...mapMutations('position', ['new']),
...mapActions('position', ['makeMove']),
...mapActions('ai', ['think', 'stop']),

winRate(score) {
return 1 / (1 + Math.exp(-score / 200))
},

scoreOf(guess, actual) {
if (actual == null) return 0
const w1 = this.winRate(guess)
const w2 = this.winRate(actual)
const diff = Math.abs(w1 - w2)
return Math.max(0, Math.round(200 * (0.5 - diff)))
},

calculateScore() {
this.lastRoundScore = this.scoreOf(this.guessScore, this.actualScore)
this.totalScore += this.lastRoundScore
},

appendInput(char) {
if (char === '-') {
if (this.guessInput === '') {
this.guessInput = '-'
} else if (this.guessInput.startsWith('-')) {
this.guessInput = this.guessInput.substring(1)
} else {
this.guessInput = '-' + this.guessInput
}
} else {
if (this.guessInput === '0' || this.guessInput === '-0') {

this.guessInput = this.guessInput.replace(/0$/, char)
} else {
this.guessInput += char
}
}
},

deleteInput() {
if (this.guessInput.length > 0) {
this.guessInput = this.guessInput.slice(0, -1)
}
},

submitGuess() {
const val = parseInt(this.guessInput)
if (isNaN(val)) return

this.guessScore = val
this.gameState = 'calculating'
this.currentDepth = 0
this.think()
},

nextProblem() {
this.currentProblemIndex++
this.gameState = 'guessing'
this.guessInput = ''
this.generateProblem()
},

restartGame() {
this.currentProblemIndex = 1
this.totalScore = 0
this.gameState = 'guessing'
this.guessInput = ''
this.generateProblem()
},

onClick() {},

checkLine(board, pos, delta, size) {
const piece = board[pos[1] * size + pos[0]];
let count = 1;

let x = pos[0] + delta[0];
let y = pos[1] + delta[1];
while (x >= 0 && x < size && y >= 0 && y < size && board[y * size + x] === piece) {
count++;
x += delta[0];
y += delta[1];
}

x = pos[0] - delta[0];
y = pos[1] - delta[1];
while (x >= 0 && x < size && y >= 0 && y < size && board[y * size + x] === piece) {
count++;
x -= delta[0];
y -= delta[1];
}
return count;
},

hasFive(board, size) {




for(let y=0; y<size; y++) {
for(let x=0; x<size; x++) {
const idx = y*size + x;
if(board[idx] === 0) continue;


const directions = [[1,0], [0,1], [1,1], [1,-1]];
for(let d of directions) {
if (this.checkLine(board, [x,y], d, size) >= 5) return true;
}
}
}
return false;
},

generateProblem() {
let valid = false
let attempts = 0

while (!valid && attempts < 20) {
attempts++

this.new(15)

let moves = []
const center = 7


moves.push([center, center])


moves.push(this.getRandomMoveInRect(center, 1, moves))


moves.push(this.getRandomMoveInRect(center, 2, moves))


moves.push(this.getRandomMoveInRect(center, 3, moves))


let m5 = this.getRandomMoveInRect(center, 4, moves)
if (m5) moves.push(m5)


let m6 = this.getRandomMoveInRect(center, 5, moves)
if (m6) moves.push(m6)


let m7 = this.getRandomMoveInRect(center, 6, moves)
if (m7) moves.push(m7)
if (moves.length < 7) continue




const tempBoard = new Array(15*15).fill(0);
let isFive = false;
for(let i=0; i<moves.length; i++) {
const m = moves[i];
const piece = (i % 2) + 1; 
tempBoard[m[1]*15 + m[0]] = piece;


const directions = [[1,0], [0,1], [1,1], [1,-1]];
for(let d of directions) {
if (this.checkLine(tempBoard, m, d, 15) >= 5) {
isFive = true;
break;
}
}
if(isFive) break;
}

if (!isFive) {
valid = true

for (const move of moves) {
this.makeMove(move)
}
}
}
},

getRandomMoveInRect(center, radius, existingMoves) {
const min = Math.max(0, center - radius)
const max = Math.min(14, center + radius)
const width = max - min + 1

let attempts = 0
while (attempts < 50) {
const x = Math.floor(Math.random() * width) + min
const y = Math.floor(Math.random() * width) + min

if (!existingMoves.some(m => m[0] === x && m[1] === y)) {
return [x, y]
}
attempts++
}
return null
}
},
i18n: {
messages: {
'zh-CN': {
valGuesser: {
problem: '',
totalScore: '',
yourEstimate: '',
submit: '',
calculating: '',
actualScore: '',
yourGuess: '',
score: '',
next: '',
finished: '',
restart: ''
}
},
'en': {
valGuesser: {
problem: 'Problem',
totalScore: 'Total Score',
yourEstimate: 'Estimate',
submit: 'Submit',
calculating: 'Verifying',
actualScore: 'Actual Score',
yourGuess: 'Your Guess',
score: 'Score',
next: 'Next',
finished: 'Finished!',
restart: 'Restart'
}
}
}
}
}
